* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.bodybutton {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stylebutton {
  color: #020202;
  cursor: pointer;
  background-color: #fff;
  border-width: 1px;
  border-color: #020202;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.stylebutton:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.timer {
  justify-content: left;
  align-items: center;
  height: 150px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  padding-right: 15px;
  display: flex;
}

.value {
  text-align: center;
  font-size: 45px;
  font-weight: bold;
}

.label {
  text-align: center;
  font-size: 20px;
}

